import { Component, ViewChild, Input, AfterViewInit } from "@angular/core";
import { DatePipe } from "@angular/common";
import { UserService } from "../services/service";
import { FilterChangeService, DateService } from "../services/common.service";

import { MatPaginator, MatTableDataSource } from "@angular/material";
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA, MatSortable} from '@angular/material';

import { Observable, from } from "rxjs";
import { first } from "rxjs/operators";

import { JiraData } from "../services/jira-data";
import { clearModulesForTest } from '@angular/core/src/linker/ng_module_factory_loader';

import { ProjectReportComponent } from "../project-sprint-report/project-report.component";

declare var AP: any;

@Component({
  selector: "sprint-report-component",
  templateUrl: "./project-summary-report.component.html",
  styleUrls: ["./project-summary-report.component.css"],
  providers: [DatePipe]
})
export class ProjectSummaryReportComponent implements AfterViewInit { 
 
  displayedColumnsHeader = ['Sprint Name', 'Estimated Hours', 'Logged Hours', 'Work Ratio', 'Start Date', 'End Date', 'Complete Date', 'Remarks'];;
  displayedColumns = ['name', 'totalEstimatedHours', 'totalHours', 'workratio', 'startDate', 'endDate', 'completeDate', 'remarks'];;
  noRecordFound = false;
  noProjectId = false
  showTable = false;
  @Input() _sprintData;
  @Input()
  set sprintData(data) {
    console.log('prev value:namename: ', data);
    this._sprintData = data.filterValues;
    this.getSprintData();  
  }
  responseObject = {};
  dataSource = new MatTableDataSource([]);
  constructor(
    private userService: UserService,
    private datePipe: DatePipe,
    private dateService: DateService,
    public dialog: MatDialog,
    private filterChangeService: FilterChangeService
  ) {}
  
  ngOnInit() { }
  
  ngAfterViewInit() {
    this.getSprintData(); 
  }

  getSprintData() {
    console.log('sprintData', this._sprintData)
    
    this.noRecordFound = false;
    // project summery
    if (this._sprintData.projectIds) { 
      this.noProjectId = false;
      this.userService.getSprintOfProject(this._sprintData.projectIds).then((searchData: any) => {
        this.filterChangeService.isLoading.emit({isLoading: false});
        console.log('search data', searchData)
        this.responseObject = searchData;
        if(!searchData.list || searchData.list.length===0) {
          this.noRecordFound = true;
          this.showTable = false;
        } else { 
          this.noRecordFound = false;
          this.showTable = true;
          this.dataSource = new MatTableDataSource(searchData.list);
          this.sortDataSource('state', 'active');
        }
      })
    } else { 
      this.noProjectId = true;
      this.showTable = false;
    }
  }
  sortDataSource(columnName: string, columnData: string) { 
      this.dataSource.data.sort((a: any, b: any) => {
          if (a.state === 'active') {
              return -1;
          } else if (a.state !== 'active') {
              return 1;
          } else {
              return 0;
          }
      });
  }
  footerDataFormat(columnName) { 
    if(columnName === 'totalEstimatedHours') {
      return this.responseObject['originalest'];
    }
    if(columnName === 'totalHours') {
      return this.responseObject['timespent'];
    }
    
    if(columnName === 'workratio') {
      return this.responseObject['workRatio'];
    }
    
    if(columnName === 'remarks') {
      return this.responseObject['remarks'];
    }
  }
  onSprintIdClick(sprintData){
    console.log('sprintData', sprintData);
    this._sprintData.selectedSprintId = sprintData.id;
    this.filterChangeService.projectSprintDetails.emit(this._sprintData)
  }
  formatDate(disCol, columnDate) { 
    if(columnDate) { 
      return this.datePipe.transform(new Date(columnDate), this.dateService.dateFormatForMenuFilterDate);
    }
    return ''; 
  }

  sprintDetailDialog(sprintData): void {
    
    this._sprintData.selectedSprintId = sprintData.id; 
    const dialogRef = this.dialog.open(ProjectReportComponent, {
      width: '800px',
      height: '500px',
      data: {filterValues: this._sprintData}
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      // this.animal = result;
    });
  }
}
