    getIssuesByUserKey(userKey) {
        const users = [];
        let myPromise = new Promise((resolve, reject) => {
            // let jql = `jql%3Dassignee%3D${userKey}%20order%20by%20created%20DESC%0A`;
            let jql = '';
            if (userKey) {
                jql = `jql=assignee=${userKey}`;
            } else {
                jql = `jql=assignee=currentuser()`;
            }
            return AP.request(
                '/rest/api/2/search?' + jql // + '&fields=closedSprints,sprint,project'
            )
                .then((data: any) => {
                    resolve(JSON.parse(data.body));
                })
                .catch((err: any) => {
                    reject(err);
                });
        });
        return myPromise;
    }

    getAllUsers(): Observable<any> {
        const users = [];
        let myPromise = new Promise((resolve, reject) => {
            return AP.request(
                '/rest/api/2/user/search?query=&startAt=0&maxResults=100&includeInactive=false'
            )
                .then((data: any) => {
                    JSON.parse(data.body).forEach((user: any) => {
                        if (user['name'].split('_')[0] != 'addon') {
                            users.push({
                                displayName: user.displayName,
                                key: user.key
                            });
                        }
                    });
                    resolve(users);
                })
                .catch((err: any) => {
                    reject(err);
                });
        });
        return from(myPromise);
    }

    getAllProjects() {
        const projects = [];
        let myPromise = new Promise((resolve, reject) => {
            return AP.request('/rest/api/2/project')
                .then(data => {
                    JSON.parse(data.body).forEach(project => {
                        projects.push({
                            name: project.name,
                            key: project.key
                        });
                    });
                    projects.sort((a, b) => (a.name > b.name) ? 1 : ((b.name > a.name) ? -1 : 0));
                    resolve(projects);
                })
                .catch((err: any) => {
                    reject(err);
                });
        });
        return from(myPromise);
    }

    // will all version of a project
    async getReleaseOfProject(projectId) {
        // get all boards
        var t = await this._getBoardId();

        // get list of board ids filter by projectId
        var tt = _get_Board_Id(projectId, t);

        // get all sprint of project
        let projectVersions: any = await _get_version_release(tt);
        console.log('projectVersions', projectVersions);
        projectVersions = projectVersions.flat();

        // // filter sprints by unique id, as sprints were in differnt board and can be duplicate so filter
        // // also filter only active + completed sprints
        let uniq = {};
        projectVersions = projectVersions.filter(obj => !uniq[obj.id] && (uniq[obj.id] = true));

        return projectVersions;
    }

    // will call from project summary Componenet
    async getSprintOfProject(projectId) {
        // get all boards
        var t = await this._getBoardId();

        var tt = _get_Board_Id(projectId, t);

        // get all sprint of project
        let sprintId: any = await _get_sprints_id(tt);
        if (sprintId.length <= 0) {
            return [];
        }
        sprintId = sprintId.flat();

        // filter sprints by unique id, as sprints were in differnt board and can be duplicate so filter
        // also filter only active + completed sprints
        let uniq = {};
        sprintId = sprintId.filter(obj => !uniq[obj.id] && (uniq[obj.id] = true) && (obj.state === 'closed' || obj.state === 'active'));

        let s = await _get_sprint_issues(sprintId);
        // console.log("s  ", s);
        return s;
    }


