// get version list by board ids
async function _get_version_release(ids) {
    return new Promise(function (resolve, reject) {
        const promiseList = [];
        for (let index = 0; index < ids.length; index++) {
            let getSprintByIdPromise = new Promise(function (resolve, reject) {
                var url = '/rest/agile/1.0/board/' + ids[index] + '/version';
                AP.request(url, function (err, response, body) {
                    if (err) {
                        console.log(err);
                        reject(err);
                    }
                    const temp = JSON.parse(body);
                    resolve(temp.values);
                });
            });
            promiseList.push(getSprintByIdPromise);
        }
        Promise.all(promiseList)
            .then(function (values) {
                return resolve(values);
            })
            .catch(error => {
                console.log('version:promise error:', error);
            });
    });
}

async function _get_sprints_id(ids) {
    return new Promise(function (resolve, reject) {
        const promiseList = [];
        for (let index = 0; index < ids.length; index++) {
            let getSprintByIdPromise = new Promise(function (resolve, reject) {
                var url = '/rest/agile/1.0/board/' + ids[index] + '/sprint';
                AP.request(url, function (err, response, body) {
                    if (err) {
                        console.log(err);
                        // reject(err);
                        // just send empty array it will show no records found
                        resolve([]);
                    }
                    const temp = JSON.parse(body);
                    resolve(temp.values);
                });
            });
            promiseList.push(getSprintByIdPromise);
        }
        Promise.all(promiseList)
            .then(function (values) {
                return resolve(values);
            })
            .catch(error => {
                console.log('_get_sprints_id:promise error:', error);
            });
    });
}

async function _get_sprint_issues(sprintId) {
    return new Promise(function (resolveMain, rejectMain) {
        let promiseList = [];
        for (let index_i = 0; index_i < sprintId.length; index_i++) {
            var t = new Promise(function (resolve, reject) {
                // var url = "/rest/agile/1.0/board/" + sprintId[index_i].originBoardId + "/sprint/" + sprintId[index_i].id + "/issue";
// let jql = 'fields%3DclosedSprints%2Cassignee%2Caggregatetimespent%2Caggregatetimeoriginalestimate%2Caggregatetimeestimate%2Caggregateprogress%2Ccreator';
                let jql = `fields=project,timeoriginalestimate,state,timespent,issuetype,timetracking,closedSprints,sprint,resolution,summary,priority,status,parent,issuelinks,worklog,customfield_10200,customfield_11400,customfield_10007`;
                var url = '/rest/agile/1.0/sprint/' + sprintId[index_i].id + '/issue?' + jql;
                AP.request(url)
                    .then((data: any) => {
                        let temp: any = {
                            issues: []
                        };
                        try {
                            temp = JSON.parse(data.body);
                        } catch (e) {
                            console.log(e);
                        } finally {
                            resolve(temp.issues);
                        }
                    })
                    .catch((err: any) => {
                        reject(err);
                    });
            });
            promiseList.push(t);
        }
        Promise.all(promiseList)
            .then(function (values) {
                let sprints = [];
                var totalest = 0;
                var totalwork = 0;

                values.forEach(element => {
                    let sprint = {
                        name: '',
                        startDate: '',
                        endDate: '',
                        completeDate: '',
                        totalEstimatedHours: 0,
                        totalHours: 0,
                        id: '',
                        workratio: 0,
                        remarks: '',
                        color: '',
                        state: ''
                    };
                    totalest = 0;
                    totalwork = 0;
                    if (element.length != 0) {
                        element.forEach(e => {
                            totalwork = totalwork + e.fields.timespent;
                            totalest = totalest + e.fields.timeoriginalestimate;
                            let sprintE = e.fields.sprint;
                            if (sprintE) {
                                (sprint.id = sprintE.id),
                                    (sprint.name = sprintE.name),
                                    (sprint.startDate = sprintE.startDate),
                                    (sprint.endDate = sprintE.endDate),
                                    (sprint.completeDate = sprintE.completeDate),
                                    (sprint.totalEstimatedHours = parseFloat(
                                        (totalest / 3600).toFixed(2)
                                    )),
                                    (sprint.totalHours = parseFloat((totalwork / 3600).toFixed(2))),
                                    (sprint.remarks = ''),
                                    (sprint.color = '');
                                (sprint.state = sprintE.state);
                            }

                            if (e.fields && e.fields.closedSprints && e.fields.closedSprints.length > 0) {
                                let closedSprint = e.fields.closedSprints[0];
                                (sprint.id = closedSprint.id),
                                    (sprint.name = closedSprint.name),
                                    (sprint.startDate = closedSprint.startDate),
                                    (sprint.endDate = closedSprint.endDate),
                                    (sprint.completeDate = closedSprint.completeDate),
                                    (sprint.totalEstimatedHours = parseFloat(
                                        (totalest / 3600).toFixed(2)
                                    )),
                                    (sprint.totalHours = parseFloat((totalwork / 3600).toFixed(2))),
                                    (sprint.remarks = ''),
                                    (sprint.color = '');
                                (sprint.state = closedSprint.state);
                            }
                        });
                    }
                    if (sprint.totalEstimatedHours != 0.0 || sprint.totalHours != 0.0) {
                        sprint.workratio = parseFloat(
                            Number(sprint.totalEstimatedHours - sprint.totalHours).toFixed(2)
                        );
                        // sprint.totalEstimatedHours = String(sprint.totalEstimatedHours) + "h"
                        if (sprint.workratio < 0.0) {
                            sprint.color = 'red';
                            sprint.remarks = 'This sprint took extra hours';
                        }
                        if (sprint.workratio == 0.0) {
                            sprint.color = 'orange';
                            sprint.remarks = 'This sprint was completed on time';
                        }
                        if (sprint.workratio > 0.0) {
                            sprint.color = 'green';
                            sprint.remarks = 'This sprint was completed with in time';
                        }
                        ;
                        sprints.push(sprint);
                    }
                });
                let ut = groupBy(sprints, 'name');
                // console.log(ut);
                var result = Object.keys(ut).map(function (key) {
                    return [key, ut[key]];
                });
                let data = {
                    originalest: 0,
                    timespent: 0,
                    workRatio: 0,
                    color: '',
                    remarks: '',
                    list: []
                };
                sprints = [];
                result.forEach(element => {
                    sprints.push(element[1][0]);
                });
                data.list = sprints;
                data.list.forEach(e => {
                    data.originalest = data.originalest + e.totalEstimatedHours;
                    data.timespent = data.timespent + e.totalHours;
                });
                data.originalest = parseFloat(Number(data.originalest).toFixed(2));
                data.timespent = parseFloat(Number(data.timespent).toFixed(2));
                data.workRatio = parseFloat(
                    Number(data.originalest - data.timespent).toFixed(2)
                );
                if (data.workRatio < 0) {
                    data.color = 'red';
                    data.remarks = 'This project took extra hours';
                }
                if (data.workRatio == 0) {
                    data.color = 'orange';
                    data.remarks = 'This project was completed on time';
                }
                if (data.workRatio > 0) {
                    data.color = 'green';
                    data.remarks = 'This project was completed with in time';
                }
                let finaldata = {
                    originalest: '',
                    timespent: '',
                    workRatio: '',
                    color: '',
                    remarks: '',
                    list: []
                };
                finaldata.originalest = data.originalest + 'h';
                finaldata.timespent = data.timespent + 'h';
                finaldata.workRatio = data.workRatio + 'h';
                finaldata.remarks = data.remarks;
                data.list.forEach(e => {
                    let finalsprint = {
                        name: '',
                        totalEstimatedHours: '',
                        totalHours: '',
                        id: '',
                        workratio: '',
                        remarks: '',
                        color: '',
                        startDate: '',
                        endDate: '',
                        completeDate: '',
                        state: ''
                    };
                    finalsprint.name = e.name;
                    finalsprint.totalEstimatedHours = e.totalEstimatedHours + 'h';
                    finalsprint.totalHours = e.totalHours + 'h';
                    finalsprint.id = e.id;
                    finalsprint.workratio = e.workratio + 'h';
                    finalsprint.remarks = e.remarks;
                    finalsprint.color = e.color;
                    finalsprint.startDate = e.startDate;
                    finalsprint.endDate = e.endDate;
                    finalsprint.completeDate = e.completeDate;
                    finalsprint.state = e.state;
                    finaldata.list.push(finalsprint);
                });
                return resolveMain(finaldata);
            })
            .catch(error => {
                rejectMain(error);
                console.log('_get_sprint_issues:promise error:', error);
            });
    });
}

function _get_Board_Id(projectName, boards) {
    let ids = [];
    for (let index = 0; index < boards.values.length; index++) {
        if (boards.values[index].location.projectKey == projectName) {
            ids.push(boards.values[index].id);
        }
    }
    return ids;
}

async function _get_sprint(sprintId) {
    // console.log("next call is being called");
    var t = await new Promise(async function (resolve, reject) {
        var url = '/rest/agile/1.0/sprint/' + sprintId + '/issue';
        await AP.request(url, async function (err, response, body) {
            if (err) {
                console.log('err', err);
                reject(err);
            }
            let temp = JSON.parse(body);
            resolve(JSON.parse(body));
        });
    });
    return t;
}

