// Calculate User Tasks in Sprint
function calculateSprintDetails(t) {
    // User tasks list
    let workuser = [];

    // sprint summary
    let total = {
        originalest: 0,
        timespent: 0,
        workRatio: 0,
        color: '',
        remarks: '',
        responsible: '',
        sprintName: '',
        sprintDescription: '',
        startDate: '',
        endDate: '',
        completeDate: '',
        state: ''
    };
// task list
    var tasks = [];
    t.issues.forEach(e => {
        var task = {
            originalEstimate: '',
            timeSpent: '',
            remainingEstimate: '',
            summary: '',
            workRatio: 0,
            color: '',
            remarks: '',
            id: '',
            status: '',
            type: '',
            priority: '',
            assigne: '',
            responsible: '',
            key: ''
        };
        // set sprint name
        if (e.fields.sprint && e.fields.sprint.name) {
            let sprint = e.fields.sprint;
            total.sprintName = sprint.name;
            total.sprintDescription = sprint.goal;
            total.startDate = sprint.startDate;
            total.endDate = sprint.endDate;
            total.completeDate = sprint.completeDate;
            total.state = sprint.state;
        }
        if (e.fields && e.fields.closedSprints && e.fields.closedSprints.length > 0) {
            let closedSprint = e.fields.closedSprints[0];
            total.sprintName = closedSprint.name;
            total.sprintDescription = closedSprint.goal;
            total.startDate = closedSprint.startDate;
            total.endDate = closedSprint.endDate;
            total.completeDate = closedSprint.completeDate;
            total.state = closedSprint.state;
        }

        if (e.fields.assignee == null) {
            task.assigne = 'unassigned';
        } else {
            task.assigne = e.fields.assignee.name;
        }
        if (e.fields.customfield_12127 != null) {
            total.responsible = e.fields.customfield_12127.displayName;
        }

        let worklog_users = e.fields.worklog.worklogs.map(x => {
            let robj = {};
            (robj['key'] = x.author.key),
                (robj['name'] = x.author.displayName),
                (robj['timeoriginalestimate'] = e.fields.timetracking.originalEstimate),
                (robj['type'] = e.fields.issuetype.name),
                (robj['timespent'] = e.fields.timetracking.timeSpent),
                (robj['Worklogged'] = x.timeSpent),
                (robj['timeSpentSeconds'] = x.timeSpentSeconds),
                (robj['WorkloggedComment'] = x.comment),
                (robj['taskkey'] = e.key),
                (robj['summery'] = e.fields.summary);
            robj['timeoriginalestimateSec'] = e.fields.timeoriginalestimate;
            robj['created'] = new Date(x.created); // .toISOString().slice(0, 10);
            robj['timespentSec'] = e.fields.timespent;
            robj['responsible'] = (e.fields && e.fields.customfield_12127) ? (e.fields.customfield_12127.displayName || '') : '';
            return robj;
        });
        workuser.push(worklog_users);
        total.originalest = total.originalest + e.fields.timeoriginalestimate;
        total.timespent = total.timespent + e.fields.timespent;
        task.responsible = (e.fields && e.fields.customfield_12127) ? (e.fields.customfield_12127.displayName || '') : '';

        task.key = (e && e.key) ? (e.key || '') : '';


        task.originalEstimate = e.fields.timetracking.originalEstimate;
        task.timeSpent = e.fields.timetracking.timeSpent;
        task.remainingEstimate = e.fields.timetracking.remainingEstimate;
        task.summary = e.fields.summary;
        task.workRatio =
            e.fields.timetracking.originalEstimateSeconds -
            e.fields.timetracking.timeSpentSeconds;
        if (e.fields.timetracking.timeSpentSeconds == undefined) {
            task.workRatio = 0;
        } else {
            task.workRatio = parseFloat(Number(task.workRatio / 3600).toFixed(2));
        }
        task.status = e.fields.status.name;
        (task.type = e.fields.issuetype.name),
            (task.priority = e.fields.priority.name);

        let typeOfIssue = task.type;
        if (task.workRatio < 0) {
            task.color = 'red';
            task.remarks = `This ${typeOfIssue} took extra hours`;
        }
        if (task.workRatio == 0) {
            task.color = 'orange';
            task.remarks = `This ${typeOfIssue} was completed on time`;
        }
        if (task.workRatio > 0) {
            task.color = 'green';
            task.remarks = `This ${typeOfIssue} was completed with in time`;
        }

        task.id = e.id;
        tasks.push(task);
    });

    total.originalest = Number((total.originalest / 3600).toFixed(2));
    total.timespent = Number((total.timespent / 3600).toFixed(2));
    total.workRatio = parseFloat(
        Number(total.originalest - total.timespent).toFixed(2)
    );
    if (total.workRatio < 0) {
        total.color = 'red';
        total.remarks = 'This sprint took extra hours';
    }
    if (total.workRatio == 0) {
        total.color = 'orange';
        total.remarks = 'This sprint was completed on time';
    }
    if (total.workRatio > 0) {
        total.color = 'green';
        total.remarks = 'This sprint was completed with in time';
    }
    t = flat(workuser, 1);
    let ut = groupBy(t, 'key');
    // console.log(ut);
    var result = Object.keys(ut).map(function (key) {
        return [key, ut[key]];
    });
    // console.log(result);
    // const array = Object.values(result).reverse();
    let finaldata = {
        originalest: '',
        timespent: '',
        workRatio: '',
        color: '',
        remarks: '',
        responsible: '',
        sprintName: '',
        sprintDescription: '',
        startDate: '',
        endDate: '',
        completeDate: '',
        state: ''
    };
    finaldata.originalest = total.originalest + 'h';
    finaldata.timespent = total.timespent + 'h';
    finaldata.workRatio = total.workRatio + 'h';
    finaldata.responsible = total.responsible;
    finaldata.remarks = total.remarks;
    finaldata.sprintDescription = total.sprintDescription;
    finaldata.sprintName = total.sprintName;
    finaldata.startDate = total.startDate;
    finaldata.endDate = total.endDate;
    finaldata.completeDate = total.completeDate;
    finaldata.state = total.state;
    // console.log("finaldata",finaldata)
    // console.log("total",total)
    // console.log("tasks",tasks)
    // console.log("ut",ut)

    let sprint_details = {
        sprint_total: finaldata,
        sprint_tasks: tasks,
        user_tasks: ut
    };

    // console.log(result);
    // console.log("calculate sprints details before returning", sprint_details);
    return sprint_details;
}
