import {Component, ViewChild, Input, AfterViewInit,} from '@angular/core';
import {DatePipe} from '@angular/common';
import {UserService} from '../services/service';
import {FilterChangeService, DateService} from '../services/common.service';

import {MatPaginator, MatTableDataSource} from '@angular/material';
import {Observable, from} from 'rxjs';
import {first} from 'rxjs/operators';

import {JiraData} from '../services/jira-data';

declare var AP: any;

@Component({
    selector: 'user-report-component',
    templateUrl: './user-report.component.html',
    styleUrls: ['./user-report.component.css'],
    providers: [DatePipe]
})
export class UserReportComponent implements AfterViewInit {
    holidays = ['Sun', 'Sat'];
    issueWorkLogs;
    filterValues;
    fieldObserver: Observable<any>;
    fields;
    paginator;
    public monthNames = [
        'JAN',
        'FEB',
        'MAR',
        'APR',
        'MAY',
        'JUN',
        'JUL',
        'AUG',
        'SEP',
        'OCT',
        'NOV',
        'DEC'
    ];
    public daysName = [
        'Sunday',
        'Monday',
        'Tuesday',
        'Wednesday',
        'Thursday',
        'Friday',
        'Saturday'
    ];
    displayedColumns = [];
    WeekDayTotalHours = [];
    @Input() _sprintData;
    @Input()
    set sprintData(data) {
        console.log('prev value:namename: ', data);
        this.filterValues = data.filterValues;
        this._sprintData = data;
        this.getSprintData();
    }

    dataSource = new MatTableDataSource([]);

    constructor(
        private userService: UserService,
        private datePipe: DatePipe,
        private filterChangeService: FilterChangeService,
        private dateService: DateService,
    ) {
    }

    ngOnInit() {
        this.getAllFields();
    }

    ngAfterViewInit() {
        this.getSprintData();
    }

    getAllFields() {

    }

    getSprintData() {

        this.fieldObserver = this.userService.getAllFields();
        // only pull once
        this.fieldObserver.pipe(first()).subscribe((changeData: any) => {

            this.fields = changeData;
            this.dataSource.paginator = this.paginator;

            let TIME_SHEET_ARRAY = [];
            let userSheetWithUserWorkLog = [];
            let orderById = this._sprintData.filterValues.orderById;
            let userId = this._sprintData.filterValues.userId;
            const data = this._sprintData.data.issues;
            this.issueWorkLogs = this._sprintData.data.issueWorkLogs;
            let startDate = this._sprintData.filterValues.startDate;
            let endDate = this._sprintData.filterValues.endDate;
            let additionalIds = this._sprintData.filterValues.additionalIds;

            let today = new Date(startDate);
            let days = [];
            // this.displayedColumns = ["key", "summery"];
            this.displayedColumns = ['avatarId'];
            this.displayedColumns.push('name');


            if (additionalIds) {
                this.displayedColumns = [...this.displayedColumns, ...additionalIds];
            }

            for (let i = 0; today <= new Date(endDate); i++) {
                let completeDay = {
                    dayName: this.daysName[today.getDay()],
                    monthName: this.monthNames[today.getMonth()],
                    dayNumber: today.getDate()
                };
                let dateWithoutTimeZone = this.datePipe.transform(new Date(today), this.dateService.dateFormatForMenuFilterDate);

                if (!days[dateWithoutTimeZone]) {
                    days[dateWithoutTimeZone] = {};
                }
                days.push(dateWithoutTimeZone);
                days[dateWithoutTimeZone].dayNameCombine =
                    completeDay.dayName + completeDay.dayNumber;

                today = this.addDays(today, 1); // increment one day
            }

            // single issue
            data.forEach((dt: any) => {
                // worklog.totalWorkLogTimeInSeconds = 0;
                // console.log('dt:', dt)
                if (dt.fields.worklog.total > dt.fields.worklog.maxResults) {
                    this.getWorkLogs(dt);
                }
                let worklog =
                    dt && dt.fields && dt.fields.worklog ? dt.fields.worklog : {};

                let timeSheetObj = {
                    id: dt.id,
                    key: dt.key,
                    self: dt.self,
                    issueId: dt.issueId,
                    summary: dt.fields.summary,
                    worklog: worklog,
                    workLogTime: {totalTimeOfSelectedDuration: 0, timeInHours: ''}
                    // ...days
                };
                // console.log(timeSheetObj)
                // debugger
                if (additionalIds) {
                    additionalIds.forEach(field => {
                        timeSheetObj[field] = dt.fields[field];
                    });
                }

                worklog.worklogs.forEach(singleWorkLog => {

                    // worklog.totalWorkLogTimeInSeconds += singleWorkLog.timeSpentSeconds
                    let workLogDate = this.datePipe.transform(new Date(singleWorkLog['started']), this.dateService.dateFormatForMenuFilterDate);
                    if (!timeSheetObj[workLogDate]) {
                        timeSheetObj[workLogDate] = {
                            totalTimeOfDay: 0,
                            logTimeWithComment: []
                        };
                    }
                    let worklogAuthorName = singleWorkLog['author'];
                    if (timeSheetObj[workLogDate].totalTimeOfDay === undefined) {
                        timeSheetObj[workLogDate].totalTimeOfDay = 0;
                    }

                    // if day is relevent
                    if (days.indexOf(workLogDate) != -1) {
                        timeSheetObj[workLogDate].totalTimeOfDay += Number(
                            singleWorkLog.timeSpentSeconds
                        );
                        timeSheetObj[workLogDate].logTimeWithComment.push(singleWorkLog);
                        timeSheetObj.workLogTime.totalTimeOfSelectedDuration += Number(
                            singleWorkLog.timeSpentSeconds
                        );

                        // flat array of work log
                        userSheetWithUserWorkLog.push({
                            id: dt.id,
                            key: dt.key,
                            self: dt.self,
                            issueId: dt.issueId,
                            summary: dt.fields.summary,
                            ...singleWorkLog
                        });
                    }
                });
                timeSheetObj.workLogTime.timeInHours = this.formatWorkLog(
                    timeSheetObj.workLogTime.totalTimeOfSelectedDuration
                );
                TIME_SHEET_ARRAY.push(timeSheetObj);
            });

            this.displayedColumns = [...this.displayedColumns, ...days];
            // this.displayedColumns.push("workLogTime");
            this.displayedColumns.push('totalTimeSpent');
            if (orderById) {
                if (orderById === 'assignee') {
                    TIME_SHEET_ARRAY.sort((a, b) => {
                        if (a[orderById] && b[orderById]) {
                            return ((a[orderById].displayName || '') + '').localeCompare(((b[orderById].displayName || '') + '')) ? -1 : 1;
                        }
                        return 0;
                    });
                }
            } else {
                TIME_SHEET_ARRAY.sort((a, b) => ('' + a.attr).localeCompare(b.attr));
                TIME_SHEET_ARRAY.reverse();
            }
            let userReport = [];
            userSheetWithUserWorkLog.forEach((a) => {
                if (a.author && a.author.accountId) {
                    // if user filter is applicable and current author is not that user skip this entry
                    if (userId && userId !== a.author.key) {
                        return;
                    }
                    let accountId = a.author.accountId;
                    let avatarId = a.author.avatarUrls;
                    const userReportObj = {
                        accountId: accountId,
                        totalTimeSpent: 0,
                        userId,
                        name: '',
                        avatarId: avatarId,
                    };
                    var foundIndex = userReport.findIndex(x => x.accountId == accountId);

                    let workLogDate = this.datePipe.transform(new Date(a['started']), this.dateService.dateFormatForMenuFilterDate);
                    if (days.indexOf(workLogDate) != -1) {
                        if (foundIndex !== -1) {
                            // for existing
                            let foundURObject = userReport[foundIndex];
                            foundURObject.totalTimeSpent = foundURObject.totalTimeSpent + a.timeSpentSeconds;

                            if (!foundURObject[workLogDate]) {
                                foundURObject[workLogDate] = {};
                            }
                            if (foundURObject[workLogDate].totalTimeOfDay === undefined) {
                                foundURObject[workLogDate].totalTimeOfDay = 0;
                            }
                            foundURObject[workLogDate].totalTimeOfDay += Number(a.timeSpentSeconds);
                        } else {

                            // for new object
                            userReportObj.totalTimeSpent = userReportObj.totalTimeSpent + a.timeSpentSeconds;
                            userReportObj.name = a.author.displayName;
                            userReportObj.userId = a.author.key;
                            if (!userReportObj[workLogDate]) {
                                userReportObj[workLogDate] = {};
                            }
                            if (userReportObj[workLogDate].totalTimeOfDay === undefined) {
                                userReportObj[workLogDate].totalTimeOfDay = 0;
                            }

                            userReportObj[workLogDate].totalTimeOfDay += Number(a.timeSpentSeconds);
                            userReport.push(userReportObj);
                        }
                    }
                }
            });
            console.log('userReport', userReport);
            this.WeekDayTotalHours = userReport;
            // this.dataSource = new MatTableDataSource(TIME_SHEET_ARRAY);
            userReport = userReport.sort((a, b) => (a.name.toLowerCase() > b.name.toLowerCase()) ? 1 : ((b.name.toLowerCase() > a.name.toLowerCase()) ? -1 : 0));
            this.dataSource = new MatTableDataSource(userReport);

        });
    }

    addDays(date, days) {
        const result = new Date(date);
        result.setDate(date.getDate() + days);
        return result;
    }

    getWorkLogs(signleIssue) {
        this.issueWorkLogs.forEach(workLog => {
            if ((workLog.issueKey == signleIssue.key)) {
                signleIssue.fields.worklog = workLog.issueWorkLog;
            }
        });
    }

    formatWorkLog(workLogs) {
        if (workLogs <= 0) {
            return '';
        }
        workLogs = Number(workLogs / 3600);
        if (Math.round(workLogs) !== workLogs) {
            workLogs = workLogs.toFixed(2);
            return Number.parseFloat(workLogs) + 'h';
        }
        // workLogs = Number.parseFloat(workLogs)
        return workLogs + 'h';
    }

    sum(key) {
        // console.log('Hours', key);
        // console.log('userReport latest', this.WeekDayTotalHours);
        if (!this.WeekDayTotalHours || !this.WeekDayTotalHours.reduce) {
            return 0;
        }
        return this.WeekDayTotalHours.reduce((a, b) => {
            if (b[key]  && (b[key].totalTimeOfDay || b.totalTimeSpent)) {
                return a + (Number(b[key].totalTimeOfDay) || Number(b.totalTimeSpent) || 0);
            }
            return a;
        }, 0);
    }

    goToIssue(issue) {
        // AP.navigator.go('issue', {
        //   issueKey: issue.key
        // });
    }

    formatCloumnName(columnName) {
        if (columnName === 'totalTimeSpent') {
            return 'Total';
        }
        if (columnName === 'name') {
            return 'Name';
        }
        if (columnName === 'avatarId') {
            return '';
        }
        // if (columnName === "customfield_12131") {
        //   return "Issue Color";
        // }

        this.fields.forEach(field => {
            if (field.key === columnName) {
                columnName = field.name;
            }
        });
        let dateObj = new Date(columnName);

        if (Object.prototype.toString.call(dateObj) === '[object Date]') {
            // it is a date
            if (isNaN(dateObj.getTime())) {
                // d.valueOf() could also work
                // date is not valid
            } else {
                // date is valid
                return this.datePipe.transform(columnName, 'EEE d/MMM');
            }
        }
        return columnName;
    }

    showWeek(week) {
        this.filterChangeService.changeWeek.emit({week: week});
    }

    // show this users detail
    onClickRow(columnData) {
        console.log('co', columnData);
        this.filterValues.userKey = columnData.userId;
        this.filterValues.userName = columnData.name;
        this.filterChangeService.userTaskDetails.emit(this.filterValues);
    }

    isCurrentDate(columnName) {
        return columnName === this.datePipe.transform(new Date(), this.dateService.dateFormatForMenuFilterDate);
    }

    function;

    isEven(value) {
        if (value % 2 === 0) {
            return true;
        } else {
            return false;
        }
    }


}
