    ngOnInit() {
        this.getDate();
        this.userCheck = false;
        this.usersObserver = this.userService.getAllUsers();
        this.projectObserver = this.userService.getAllProjects();
        this.fieldObserver = this.userService.getAllFields();
        this.selectReport = [
            {
                key: 'projectSummary',
                name: 'Project Summary'
            },
            {
                key: 'projectSprintReport',
                name: 'Project Sprint Report'
            },
            {
                key: 'taskReport',
                name: 'Task Report'
            },
            {
                key: 'userReport',
                name: 'User Report'
            },
            {
                key: 'projectRelease',
                name: 'Releases'
            },
            {
                key: 'todoReport',
                name: 'To Do Report'
            }
        ];
        // This event will call from project summary report
        // And trigger filter for project sprint summary
        this.filterChangeService.projectSprintDetails.subscribe((filter: any) => {
            console.log('filtering content', filter);
            // selectedSprintId
            this.selectedSprintId = filter.selectedSprintId;
            console.log('filter sprint id', filter.selectedSprintId);
            this.selectedReportType = 'projectSprintReport';
            this.selectedProjectObj = filter.selectedProjectObj;

            this.filter();
            // just show sprints of projects to be selected
            this.getSprintForProject(filter.selectedProjectObj);
            this.selectedSprintId = filter.selectedSprintId;
        });

        this.filterChangeService.backbutton.subscribe((filter: any) => {
            localStorage.clear();
            localStorage.setItem('formStateReportType', filter);
            this.selectedUserId = null;
            this.selectedReportType = filter;
            this.filter();
        });


        // This event will call from user report
        this.filterChangeService.userTaskDetails.subscribe((filter: any) => {
            console.log('filtertaskReport 111111111', filter);
            if (filter.userKey) {
                this.selectedUserId = filter.userKey;
                if (!this.selectedUserobj) {
                    this.selectedUserobj = {};
                }
                this.selectedUserobj.displayName = filter.userName;
            }
            this.selectedReportType = 'taskReport';

            // Call the method
            this.filter();
            // Store data in local storage for Maintaing Form State
            this.setLocalReportType();
            let searchUser = {displayName: filter.userName, key: filter.userKey};
            this.setLocalSearchUser(searchUser);
            this.buttonFlag = true;
            console.log('Button Flag True', this.buttonFlag);
        });

        // update filter if call from other controller
        this.filterChangeService.changeWeek.subscribe((week: any) => {
            console.log('week.week', week.week);

            // if selected is current week
            if (week.week === 'current') {
                this.weekCounter = 0;
                this.getDate();
            }
            if (week.week === 'previous') {
                // run for next/previous date that is calculated in above if statment
                let cuurentDateOne = new Date(this.startDate);
                let CurrentDateTwo = new Date(this.startDate);
                let first = cuurentDateOne.getDate() - cuurentDateOne.getDay() - 6;
                let firstDate = new Date(cuurentDateOne.setDate(first));
                let last = first + 6;
                let lastDate = new Date(CurrentDateTwo.setDate(last));
                this.startDate = this.datePipe.transform(firstDate, this.dateService.dateFormatForMenuFilterDate);
                this.endDate = this.datePipe.transform(lastDate, this.dateService.dateFormatForMenuFilterDate);

            }
            if (week.week === 'next') {
                // run for next/previous date that is calculated in above if statment
                let cuurentDateOne = new Date(this.endDate);
                let CurrentDateTwo = new Date(this.endDate);
                console.log('cuurentDateOne', cuurentDateOne);
                console.log('CurrentDateTwo', CurrentDateTwo);
                let first;
                console.log('cuurentDateOne.getDate()', cuurentDateOne.getDate());
                if (cuurentDateOne.getDay() === 0) {
                    first = cuurentDateOne.getDate() - cuurentDateOne.getDay() + 1;
                    console.log('first', first);
                } else {
                    first = cuurentDateOne.getDate() - cuurentDateOne.getDay() + 8;
                    console.log('first', first);
                }
                let firstDate = new Date(cuurentDateOne.setDate(first));
                console.log('firstDate', firstDate);
                let last = first + 6;
                console.log('last', last);
                let lastDate = new Date(CurrentDateTwo.setDate(last));
                this.startDate = this.datePipe.transform(firstDate, this.dateService.dateFormatForMenuFilterDate);
                this.endDate = this.datePipe.transform(lastDate, this.dateService.dateFormatForMenuFilterDate);

            }


            this.filter();

        });
        // this.filter();


    }

