    resetsprint() {
        this.sprintObserver = null;
        this.selectedSprintId = null;
    }

    filter() {
        let startDate = this.datePipe.transform(this.startDate, this.dateService.dateFormatForMenuFilterDate);
        let endDate = this.datePipe.transform(this.endDate, this.dateService.dateFormatForMenuFilterDate);

        const filterData = {
            reportTYpe: this.selectedReportType,
            userId: this.selectedUserId,
            selectedUserobj: this.selectedUserobj,
            selectedProjectObj: this.selectedProjectObj,
            projectIds: this.selectedProjectId,
            CategoryId: this.selectCategoryFieldId,
            orderById: this.selectOrderByField,
            additionalIds: this.selectAdditionalFieldId,
            groupfieldId: this.selectFieldId,
            selectedSprintId: this.selectedSprintId,
            startDate: startDate,
            endDate: endDate,
            buttonFlag: this.buttonFlag,
        };

        // let workLogDate = this.datePipe.transform(, this.dateService.dateFormatString);
        if (this.selectedProjectId && !Array.isArray(this.selectedProjectId)) {
            filterData.projectIds = [this.selectedProjectId];
        }
        this.filterChangeService.isLoading.emit({isLoading: true});
        console.log('filterData.projectIds', filterData.projectIds);

        if (this.selectedReportType === 'projectSprintReport') {
            if (!this.selectedProjectId || !this.selectedSprintId) {
                this.filterChangeService.isLoading.emit({isLoading: false});
            }
            this.filterChangeService.change.emit({
                type: '',
                filterValues: filterData,
                data: ''
            });
        }

        if (this.selectedReportType === 'projectSummary') {
            if (!this.selectedProjectId) {
                this.filterChangeService.isLoading.emit({isLoading: false});
            }

            this.filterChangeService.change.emit({
                type: '',
                filterValues: filterData,
                data: ''
            });
        }
        if (this.selectedReportType === 'taskReport' || this.selectedReportType === 'userReport') {

            this.searchObserver = this.userService.getSearch(filterData);
            this.searchObserver.subscribe(searchData => {
                this.filterChangeService.isLoading.emit({isLoading: false});
                this.filterChangeService.change.emit({
                    type: '',
                    filterValues: filterData,
                    data: searchData
                });
            });

        }

        if (this.selectedReportType === 'projectRelease') {

            if (!this.selectedProjectId) {
                this.filterChangeService.isLoading.emit({isLoading: false});
            }
            this.filterChangeService.change.emit({
                type: '',
                filterValues: filterData,
                data: {}
            });
        }

        if (this.selectedReportType === 'todoReport') {

            if (!this.selectedUserId) {
                this.filterChangeService.isLoading.emit({isLoading: false});
            }
            this.filterChangeService.change.emit({
                type: '',
                filterValues: filterData,
                data: {}
            });
        }
    }

    emptyView() {
        const data = {'emptyView': 'emptyView'};
        this.filterChangeService.emptyView.emit({
            type: '',
            filterValues: data,
            data: {}
        });
    }

    resetfilter() {
        // this.selectedReportType = null;
        this.selectedUserId = null;
        this.selectedProjectId = null;
        this.selectFieldId = null;
        this.selectCategoryFieldId = null;
        this.selectAdditionalFieldId = null;
        this.selectOrderByField = null;
        this.selectedSprintId = null;
        this.selectedProjectObj = null;
        this.selectedUserobj = null;
        this.getDate();
        this.emptyView();
    }


    setLocalReportType() {
        localStorage.clear();
        if (this.selectedReportType === 'taskReport') {
            this.buttonFlag = false;
            console.log('Button Flag:', this.buttonFlag);
        }
        if (this.selectedReportType === 'userReport') {
            this.buttonFlag = true;
            console.log('Button Flag:', this.buttonFlag);
        }
        localStorage.setItem('formStateReportType', this.selectedReportType);

    }


    setLocalSearchUser(event) {
        if (event !== 'undefined') {
            localStorage.setItem('formStateSearchUser', JSON.stringify(event));
            console.log('Hi Im from Search User', event);
        }
    }

    setLocalProjectFilter(event) {
        console.log('Project testing', event);
        localStorage.setItem('formStateProjectFilter', JSON.stringify(event));
    }

    setLocalProjectSprint(event) {
        localStorage.setItem('formStateProjectSprint', this.selectedSprintId);
    }

    setLocalStartDate() {
        console.log(this.startDate.toISOString());
        localStorage.setItem('formStateStartDate', this.startDate.toISOString());
    }

    setLocalEndDate() {
        console.log(this.endDate.toISOString());
        localStorage.setItem('formStateEndDate', this.endDate.toISOString());
    }

    setLocalAdditionalFields(event) {
        localStorage.setItem('formStateAdditionalFields', JSON.stringify(event));
    }

    getDate() {
        var curr = new Date(); // get current date
        var curr2 = new Date(); // get current date
        console.log('curr.getDate()', curr);
        console.log('curr.getDay()', curr2);
        if (curr.getDay() !== 0) {
            var first = curr.getDate() - curr.getDay() + 1; // First day is the day of the month - the day of the week
        } else {
            var first = curr.getDate() - 7 + 1; // First day is the day of the month - the day of the week
        }
        var last = first + 6; // last day is the first day + 6
        var firstday = new Date(curr.setDate(first));

        var lastday = new Date(curr2.setDate(last));
        this.startDate = firstday;
        this.endDate = lastday;
    }

    dateInput() {
        return '';
    }

