    ngAfterViewInit() {
        AP.resize('100%', 'calc(100vh - 160px)');
        //Setting Form State
        let getLocalReportType = localStorage.getItem('formStateReportType');
        let getLocalSearchUser = JSON.parse(localStorage.getItem('formStateSearchUser'));
        let getLocalProjectFilter = JSON.parse(localStorage.getItem('formStateProjectFilter'));
        let getLocalAdditionalFields = JSON.parse(localStorage.getItem('formStateAdditionalFields'));
        let getLocalProjectSprint = (localStorage.getItem('formStateProjectSprint'));
        let getLocalStartDate = (localStorage.getItem('formStateStartDate'));
        let getLocalEndDate = (localStorage.getItem('formStateEndDate'));
        setTimeout(() => {
                //User Report
                if (getLocalReportType) {
                    this.selectedReportType = getLocalReportType;
                    console.log('Report Type= ', getLocalReportType);
                }
                if (getLocalSearchUser) {
                    this.selectedUserobj = getLocalSearchUser;
                    this.selectedUserId = getLocalSearchUser.key;
                    console.log('User Search= ', getLocalSearchUser.key);
                }
                if (getLocalProjectFilter) {
                    this.selectedProjectObj = getLocalProjectFilter;
                    let arrLength = getLocalProjectFilter.length;
                    if (Array.isArray(getLocalProjectFilter) && arrLength > 0) {
                        let localProjectFilterArr: string[] = [];
                        for (let i = 0; i < arrLength; i++) {
                            localProjectFilterArr.push(getLocalProjectFilter[i].key);
                        }
                        console.log('Selected Project= ', localProjectFilterArr);
                        this.selectedProjectId = localProjectFilterArr;

                    } else {
                        this.selectedProjectId = getLocalProjectFilter.key;
                        this.getSprintForProject(getLocalProjectFilter);
                    }
                }
                if (getLocalProjectSprint) {
                    this.selectedSprintId = Number(getLocalProjectSprint);
                    console.log('Selected Sprint= ', Number(getLocalProjectSprint));

                }
                if (getLocalAdditionalFields) {
                    let arrLength = getLocalAdditionalFields.length;
                    if (arrLength > 0) {
                        let localAdditionalFieldsArr: string[] = [];
                        for (let i = 0; i < arrLength; i++) {
                            localAdditionalFieldsArr.push(getLocalAdditionalFields[i].key);
                        }
                        this.selectAdditionalFieldId = localAdditionalFieldsArr;
                        console.log('Additional Fields= ', localAdditionalFieldsArr);

                    }
                }
                if (getLocalStartDate) {
                    this.startDate = getLocalStartDate;
                    console.log('Start Date= ', getLocalStartDate);

                }
                if (getLocalEndDate) {
                    this.startDate = getLocalEndDate;
                    console.log('End Date= ', getLocalStartDate);

                }
                if (getLocalReportType !== null) {
                    this.filter();
                }
            },
            2000);

    }


    onChangeUser(selectedUser) {
        this.selectedUserobj = selectedUser;

    }

    getSprintForProject(selectedProject) {

        this.selectedProjectObj = selectedProject;
        if (selectedProject && Array.isArray(selectedProject)) {
            this.selectedProjectObj = selectedProject[0];
        }
        if (selectedProject && this.selectedReportType === 'projectSprintReport') {
            this.sprintLoading = true;
            this.sprintObserver = null;
            this.selectedSprintId = null;
            this.userService.getSprintNamesOfProject(selectedProject.key).then((searchData: any) => {
                searchData = searchData.flat();

                // filter sprints by unique id, as sprints were in differnt board and can be duplicate so filter
                // also filter only active + completed sprints
                let uniq = {};
                this.sprintObserver = searchData.filter(obj => !uniq[obj.id] && (uniq[obj.id] = true) && (obj.state === 'closed' || obj.state === 'active'));
                this.sprintLoading = false;
            });
        }
    }

