export class TodoReportComponent implements AfterViewInit { 


  displayedColumnsHeader = [ 'Key', 'Summary', 'Description', 'Status', 'Project Name', 'Responsible', 'Priority', 'Platform', 'Estimate', 'Logged Hour(s)' ];;
  displayedColumns = [ 'key', 'summary', 'description', 'status', 'projectName', 'responsible', 'priority', 'platform', 'timeoriginalestimate', 'timespent' ];;
  noRecordFound;
  noSelectedProject;
  showTable;
  @Input() _sprintData;
  @Input()
  set sprintData(data) {
    console.log('prev value:namename: ', data);
    this._sprintData = data.filterValues;
    this.getData();  
  }
  responseObject = {};
  dataSource = new MatTableDataSource([]);
  constructor(
    private userService: UserService,
    private datePipe: DatePipe,
    private dateService: DateService,
    public dialog: MatDialog,
    private filterChangeService: FilterChangeService
  ) {}
  
  ngOnInit() { }
  
  ngAfterViewInit() {
    // this.getData();
   }

  getData() {
    console.log('project release sprintData', this._sprintData)


    if (this._sprintData.userId ) { 
      this.userService.getIssuesByUserKey(this._sprintData.userId || '').then((searchData: any) => {
        console.log('project release getReleaseOfProject:', searchData)
        this.filterChangeService.isLoading.emit({isLoading: false});
        this.noSelectedProject = false;
        if(!searchData.issues || searchData.issues.length <1) {
          this.noRecordFound = true
        } else { 
          this.showTable = true; 
          let tableData = []
          searchData.issues.forEach(element => {
            let platform = element.fields.customfield_11001 ? element.fields.customfield_11001 : [];
            let p = '';
            platform.forEach((a, i) => {
              if(i !== 0) {
                p = p + ', '
              }
              p =  p + a;
            });
            tableData.push({
              key: element.key,
              summary: element.fields.summary,
              description: element.fields.description,
              status: element.fields.status.name,
              projectName: element.fields.project.name,
              responsible: element.fields.customfield_12127 ? element.fields.customfield_12127.displayName : '',
              priority: element.fields.priority.name,
              platform: p,
              timeoriginalestimate: this.formatWorkLog(element.fields.timeoriginalestimate),
              timespent: this.formatWorkLog(element.fields.timespent),
            })
          });
          tableData.reverse();
          this.dataSource = new MatTableDataSource(tableData); 
          this.noRecordFound = false
        }
      })
     
    } else {
      this.filterChangeService.isLoading.emit({isLoading: false});
      this.noSelectedProject = true
    }
  } 
 

  onSprintIdClick(sprintData){
    console.log('sprintData', sprintData);
    this._sprintData.selectedSprintId = sprintData.id;
    this.filterChangeService.projectSprintDetails.emit(this._sprintData)
  }

  formatDate(disCol, columnDate) { 
    if(columnDate) { 
      return this.datePipe.transform(new Date(columnDate), this.dateService.dateFormatForMenuFilterDate);
    }
    return ''; 
  }

  sprintDetailDialog(sprintData): void {
    
    // this._sprintData.selectedSprintId = sprintData.id; 
    // const dialogRef = this.dialog.open(ProjectReportComponent, {
    //   width: '800px',
    //   height: '500px',
    //   data: {filterValues: this._sprintData}
    // });

    // dialogRef.afterClosed().subscribe(result => {
    //   console.log('The dialog was closed');
    //   // this.animal = result;
    // });
  }
    goToIssue(issue) {
        // AP.navigator.go('issue', {
        //   issueKey: issue.key
        // });
        AP.getLocation(function (location) {
            console.log(location);
            let loc = location;
            loc = loc.split('plugins')[0];
            window.open(loc + 'browse/' + issue.key, '_blank');
        });
    }

  
  formatWorkLog(workLogs) {
    if (workLogs <= 0) {
      return "";
    }
    workLogs = Number(workLogs / 3600);
    if (Math.round(workLogs) !== workLogs) {
      workLogs = workLogs.toFixed(2);
      return Number.parseFloat(workLogs) + "h";
    } 
    return workLogs + "h";
  }
}
