import {Component, ViewChild, Input, Output, EventEmitter} from '@angular/core';
import {DatePipe} from '@angular/common';
import {UserService} from '../services/service';
import {FilterChangeService, DateService} from '../services/common.service';
import {MatPaginator, MatTableDataSource} from '@angular/material';
import {Observable, from} from 'rxjs';
import {first} from 'rxjs/operators';

import {JiraData} from '../services/jira-data';
import {isString} from 'util';
import {computeMsgId} from '@angular/compiler/src/i18n/digest';


declare var AP: any;

@Component({
    selector: 'time-sheet-component',
    templateUrl: './time-sheet.component.html',
    styleUrls: ['./time-sheet.component.css'],
    providers: [DatePipe]
})
export class TimeSheetComponent {

    showAlert(id: any) {
        this.filterChangeService.backbutton.emit(id);
    }

    displayedColumns: string[];
    holidays = ['Sun', 'Sat'];
    dataSource = new MatTableDataSource([]);
    issueWorkLogs;
    fieldObserver: Observable<any>;
    fields;
    timeSheetData;
    noRecordsFound = false;
    filterValues;
    totalIssues;

    @Input()
    set sprintData(data) {
        console.log('prev value:namename: ', data);
        this.timeSheetData = data;
        this.filterValues = data.filterValues;
        if (!data || !data.data || !data.data.issues || data.data.issues.length < 1) {
            this.noRecordsFound = true;
        } else {
            this.noRecordsFound = false;
        }

        this.getTimeSheetData();
    }


    public monthNames = [
        'JAN',
        'FEB',
        'MAR',
        'APR',
        'MAY',
        'JUN',
        'JUL',
        'AUG',
        'SEP',
        'OCT',
        'NOV',
        'DEC'
    ];
    public daysName = [
        'Sunday',
        'Monday',
        'Tuesday',
        'Wednesday',
        'Thursday',
        'Friday',
        'Saturday'
    ];

    constructor(
        private userService: UserService,
        private datePipe: DatePipe,
        private filterChangeService: FilterChangeService,
        private dateService: DateService,
    ) {
    }


    filter() {
    }

    @ViewChild(MatPaginator) paginator: MatPaginator;

    ngOnInit() {
        this.dataSource.paginator = this.paginator;
        // this.dataSource.sort = this.sort;
    }

    getTimeSheetData() {
        this.fieldObserver = this.userService.getAllFields();
        // only pull once
        this.fieldObserver.pipe(first()).subscribe((fieldsOfIssue: any) => {
            this.dataSource.paginator = this.paginator;

            let TIME_SHEET_ARRAY = [];
            let orderById = this.timeSheetData.filterValues.orderById;
            let userId = this.timeSheetData.filterValues.userId;
            const data = this.timeSheetData.data.issues;
            this.issueWorkLogs = this.timeSheetData.data.issueWorkLogs;
            let startDate = this.timeSheetData.filterValues.startDate;
            let endDate = this.timeSheetData.filterValues.endDate;
            let additionalIds = this.timeSheetData.filterValues.additionalIds;
            this.totalIssues = data.length || 0;
            let today = new Date(startDate);
            let days = [];
            this.displayedColumns = ['iconUrl', 'key', 'summary', 'parentIssue'];

            if (additionalIds) {
                this.displayedColumns = [...this.displayedColumns, ...additionalIds];
            }

            for (let i = 0; today <= new Date(endDate); i++) {
                let completeDay = {
                    dayName: this.daysName[today.getDay()],
                    monthName: this.monthNames[today.getMonth()],
                    dayNumber: today.getDate()
                };
                let dateWithoutTimeZone = this.datePipe.transform(today, this.dateService.dateFormatForMenuFilterDate);

                if (!days[dateWithoutTimeZone]) {
                    days[dateWithoutTimeZone] = {};
                }
                days.push(dateWithoutTimeZone);
                // days[dateWithoutTimeZone].dayNameCombine = completeDay.dayName + completeDay.dayNumber;

                today = this.addDays(today, 1); // increment one day
            }

            // single issue
            data.forEach((dt: any) => {
                // worklog.totalWorkLogTimeInSeconds = 0;
                if (dt.fields.worklog.total > dt.fields.worklog.maxResults) {
                    this.getWorkLogs(dt);
                }
                let worklog =
                    dt && dt.fields && dt.fields.worklog ? dt.fields.worklog : {};

                let timeSheetObj = {
                    id: dt.id,
                    key: dt.key,
                    self: dt.self,
                    status: dt.fields.status.name,
                    issueId: dt.issueId,
                    summary: dt.fields.summary,
                    parentIssue: dt.fields.customfield_10200,
                    iconUrl: dt.fields.issuetype && dt.fields.issuetype.iconUrl ? dt.fields.issuetype.iconUrl : '',
                    desc: dt.fields.issuetype.description,
                    // worklog: worklog,
                    workLogTime: {totalTimeOfSelectedDuration: 0, timeInHours: 0}
                    // ...days
                };

                if (additionalIds) {
                    additionalIds.forEach(field => {
                        timeSheetObj[field] = dt.fields[field];
                    });
                }

                worklog.worklogs.forEach(singleWorkLog => {

                    // if user filter is applicable and current author is not that user skip this entry
                    if (userId && userId !== singleWorkLog.author.key) {
                        return;
                    }
                    // worklog.totalWorkLogTimeInSeconds += singleWorkLog.timeSpentSeconds
                    let workLogDate =
                        this.datePipe.transform(new Date(singleWorkLog['started']), this.dateService.dateFormatForMenuFilterDate);


                    if (!timeSheetObj[workLogDate]) {
                        timeSheetObj[workLogDate] = {
                            totalTimeOfDay: 0,
                            // logTimeWithComment: [] // experimentatl feature
                        };
                    }
                    // console.log(workLogDate, singleWorkLog.timeSpentSeconds);
                    if (timeSheetObj[workLogDate].totalTimeOfDay === undefined) {
                        timeSheetObj[workLogDate].totalTimeOfDay = 0;
                    }
                    // console.log(workLogDate, String(dt.id), String(singleWorkLog.issueId))
                    // if(Number(singleWorkLog.timeSpentSeconds) && String(dt.id) === String(singleWorkLog.issueId)) {
                    if (days.indexOf(workLogDate) != -1) {
                        timeSheetObj[workLogDate].totalTimeOfDay += Number(
                            singleWorkLog.timeSpentSeconds);
                        // Username and log time
                        if (!timeSheetObj[workLogDate].workuserdata) {
                            timeSheetObj[workLogDate].workuserdata = [];
                        }

                        timeSheetObj[workLogDate].workuserdata.push({
                            username: singleWorkLog.author.displayName ? singleWorkLog.author.displayName : '',
                            timelog: singleWorkLog.timeSpentSeconds ? singleWorkLog.timeSpentSeconds : ''
                        });
                        // username = singleWorkLog.author.displayName;
                        // timeSheetObj[workLogDate].usertimespent = singleWorkLog.author.timeSpentSeconds;
                        // timeSheetObj[workLogDate].logTimeWithComment.push(singleWorkLog);
                        timeSheetObj.workLogTime.totalTimeOfSelectedDuration += Number(
                            singleWorkLog.timeSpentSeconds
                        );
                    }
                });

                timeSheetObj.workLogTime.timeInHours = +timeSheetObj.workLogTime
                    .totalTimeOfSelectedDuration;
                TIME_SHEET_ARRAY.push(timeSheetObj);
                console.log('TIME_SHEET_ARRAY', TIME_SHEET_ARRAY);
            });

            this.displayedColumns = [...this.displayedColumns, ...days];
            this.displayedColumns.push('workLogTime');
            if (orderById) {
                if (orderById === 'assignee') {
                    TIME_SHEET_ARRAY.sort((a, b) => {
                        if (a[orderById] && b[orderById]) {
                            return ((a[orderById].displayName || '') + '').localeCompare(
                                (b[orderById].displayName || '') + ''
                            )
                                ? -1
                                : 1;
                        }
                        return 0;
                    });
                }
            } else {
                TIME_SHEET_ARRAY.sort((a, b) => ('' + a.attr).localeCompare(b.attr));
                TIME_SHEET_ARRAY.reverse();
            }
            this.timeSheetData = TIME_SHEET_ARRAY;
            this.dataSource = new MatTableDataSource(TIME_SHEET_ARRAY);

            // first page by default
            this.dataSource.paginator = this.paginator;
            if (this.dataSource.paginator) {
                this.dataSource.paginator.firstPage();
            }
            this.fields = fieldsOfIssue;
        });
    }

    addDays(date, days) {
        const result = new Date(date);
        result.setDate(date.getDate() + days);
        return result;
    }

    getWorkLogs(signleIssue) {
        this.issueWorkLogs.forEach(workLog => {
            if ((workLog.issueKey == signleIssue.key)) {
                signleIssue.fields.worklog = workLog.issueWorkLog;
            }
        });
    }

    checkArray(value) {
        console.log('Array Value is:', Array.isArray(value));
        return value && Array.isArray(value);
    }

    sum(key) {
        // key is column name only sum if column name is either totalTimeOfDay ,timeInHours
        if (!this.timeSheetData || !this.timeSheetData.reduce) {
            return 0;
        }
        return this.timeSheetData.reduce((a, b) => {
            if (b[key] && (b[key].totalTimeOfDay || b[key].timeInHours)) {
                return a + (Number(b[key].totalTimeOfDay) || Number(b[key].timeInHours) || 0);
            }
            return a;
        }, 0);
    }

    formatWorkLog(workLogs) {
        if (workLogs <= 0) {
            return '';
        }
        workLogs = Number(workLogs / 3600);
        if (Math.round(workLogs) !== workLogs) {
            workLogs = workLogs.toFixed(2);
            return Number.parseFloat(workLogs) + 'h';
        }
        // workLogs = Number.parseFloat(workLogs)
        return workLogs + 'h';
    }

    goToIssue(issue) {
        // AP.navigator.go('issue', {
        //   issueKey: issue.key
        // });
        AP.getLocation(function (location) {
            console.log(location);
            let loc = location;
            loc = loc.split('plugins')[0];
            window.open(loc + 'browse/' + issue.key, '_blank');
        });
    }

    formatCloumnName(columnName) {
        let dontShowCoulmnNames = ['parentIssue', 'iconUrl'];
        // if coulmn name is one of above show nothing
        if (dontShowCoulmnNames.indexOf(columnName) !== -1) {
            return '';
        }
        if (columnName === 'workLogTime') {
            return 'Total';
        }

        this.fields.forEach(field => {
            if (field.key === columnName) {
                columnName = field.name;
            }
        });
        let dateObj = new Date(columnName);

        if (Object.prototype.toString.call(dateObj) === '[object Date]') {
            // it is a date
            if (isNaN(dateObj.getTime())) {
                // d.valueOf() could also work
                // date is not valid
            } else {
                // date is valid
                return this.datePipe.transform(columnName, 'EEE d/MMM');
            }
        }
        if (isString(columnName)) {
            return this.upperCaseFirstLatter(columnName);
        }

        return columnName;
    }

    isCurrentDate(columnName) {
        return columnName === this.datePipe.transform(new Date(), this.dateService.dateFormatForMenuFilterDate);
    }

    upperCaseFirstLatter(columnName) {
        return columnName.charAt(0).toUpperCase() + columnName.slice(1);
    }

    showWeek(week) {
        this.filterChangeService.changeWeek.emit({week: week});
    }

    dataRangling(temp) {
        if (temp.length > 0) {
            return temp[temp.length - 1].name;
        }
    }

    dateFormating(temp) {
        return this.datePipe.transform(temp, this.dateService.dateFormatForMenuFilterDate);
    }

    getToolTipData(disCol) {
        let data;
        let workLogs;
        if (disCol !== undefined) {
            data = '';
            workLogs = '';
            disCol.forEach(function (value) {
                workLogs = Number(value.timelog / 3600);
                if (Math.round(workLogs) !== workLogs) {
                    workLogs = workLogs.toFixed(2);
                    workLogs = Number.parseFloat(workLogs);
                }
                data += value.username + ':\xa0\xa0\xa0\xa0\xa0\xa0\xa0' + workLogs + 'h' + '\n';
            });

            // data = disCol[0].username + ':' + '\n' + disCol[0].timelog;
            return data;
        }
    }

}
