  showTable;
  @Input() _sprintData;
  @Input()
  set sprintData(data) {
    console.log('prev value:namename: ', data);
    this._sprintData = data.filterValues;
    this.getSprintData();  
  }
  responseObject = {};
  dataSource = new MatTableDataSource([]);
  constructor(
    private userService: UserService,
    private datePipe: DatePipe,
    private dateService: DateService,
    public dialog: MatDialog,
    private filterChangeService: FilterChangeService
  ) {}
  
  ngOnInit() { }
  
  ngAfterViewInit() {
    // this.getSprintData();
   }

  getSprintData() {
    console.log('project release sprintData', this._sprintData)
    
    if (this._sprintData.projectIds && this._sprintData.projectIds[0]) { 
      this.userService.getReleaseOfProject(this._sprintData.projectIds[0]).then((searchData: any) => {
        this.filterChangeService.isLoading.emit({isLoading: false});
        this.noSelectedProject = false;
        console.log('project release getReleaseOfProject:', searchData)
        if(searchData.length <1) {
          this.noRecordFound = true
        } else { 
          this.showTable = true;
          searchData = searchData.reverse()
          searchData.forEach(release => {
            release.archived = release.archived ? 'Yes': 'No'
            release.released = release.released ? 'RELEASED': 'UNRELEASED'
          });
          this.dataSource = new MatTableDataSource(searchData); 
          this.noRecordFound = false
        }
      })
    } else {
      this.filterChangeService.isLoading.emit({isLoading: false});
      this.noSelectedProject = true
    }
  } 
 

  onSprintIdClick(sprintData){
    console.log('sprintData', sprintData);
    this._sprintData.selectedSprintId = sprintData.id;
    this.filterChangeService.projectSprintDetails.emit(this._sprintData)
  }

  formatDate(disCol, columnDate) { 
    if(columnDate) { 
      return this.datePipe.transform(new Date(columnDate), this.dateService.dateFormatForMenuFilterDate);
    }
    return ''; 
  }

