import {Component, ViewChild, Input, AfterViewInit, Inject, Optional} from '@angular/core';
import {DatePipe} from '@angular/common';
import {UserService} from '../services/service';
import {FilterChangeService, DateService} from '../services/common.service';

import {MatPaginator, MatTableDataSource, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import {Observable, from} from 'rxjs';
import {first} from 'rxjs/operators';

import {JiraData} from '../services/jira-data';

declare var AP: any;

@Component({
    selector: 'project-report-component',
    templateUrl: './project-report.component.html',
    styleUrls: ['./project-report.component.css'],
    providers: [DatePipe]
})
export class ProjectReportComponent implements AfterViewInit {

    // one variable contian key and other contain Display name of table
    sprintTasksColumnsHeader = ['key', 'type', 'originalEstimate', 'timeSpent', 'remainingEstimate', 'status', 'summary', 'workRatio', 'priority', 'responsible', 'remarks'];
;
    sprintTasksHeadingFormat = ['Key', 'Type', 'Original Estimate', 'Time Spent', 'Remaining Estimate', 'Status', 'Summary', 'Work Ratio', 'Priority', 'Responsible', 'Remarks'];
;

    displayedColumns = ['id', 'name', 'remarks', 'totalEstimatedHours', 'totalHours', 'workratio'];

    sprintTotaldisplayedColumns = ['originalest', 'timespent', 'workRatio', 'startDate', 'endDate', 'completeDate', 'remarks'];
    sprintTotalHeadingFormat = ['Original Estimates', 'Time Spent', 'Work Ratio', 'Start Date', 'End Date', 'Complete Date', 'Remarks'];

    userTasksColumns = ['taskkey', 'type',
        // 'Worklogged',
        'summery', 'timeoriginalestimateSec', 'timeSpentSeconds', 'WorkloggedComment'];
    userTasksColumnsHeadingFormat = ['Key', 'Type',
        // 'Work Logged',
        'Summary', 'Estimated Hour\'s', 'Logged Hours', 'worklog Comment'];


    @Input() _sprintData;
    sprintTasks;
    sprintTotal;
    userTasks;
    noSelectedSprint;
    sprintName = '';
    sprintDescription = '';
    showCloseButton;
    showLoader;
    noRecordsFound;
    showTable;

    @Input()
    set sprintData(data) {
        this._sprintData = data;
        this.getSprintData();
    }

    responseObject = {};
    dataSource = []; //  = new MatTableDataSource([]);
    sprintTasksDataSource = new MatTableDataSource([]);
    sprintTotaldataSource = new MatTableDataSource([]);
    userTasksDataSource = new MatTableDataSource([]);

    constructor(
        private userService: UserService,
        private datePipe: DatePipe,
        private dateService: DateService,
        private filterChangeService: FilterChangeService,
        // component will also open as dialog
        // 1. cross button will appear for dialog, showCloseButton
        // 2. data will be injected using below object dialogeData
        @Optional() public dialogRef: MatDialogRef<ProjectReportComponent>,
        @Optional() @Inject(MAT_DIALOG_DATA) public dialogeData
    ) {
    }

    ngOnInit() {
    }

    ngAfterViewInit() {
        this.getSprintData();
    }

    getSprintData() {
        // if filter is coming from dialoge data set it
        // this component will also open as dialog
        if (this.dialogeData) {
            // set filter data here
            this._sprintData = this.dialogeData;
            this.showCloseButton = true;
            this.showLoader = true;
        }

        this.sprintTotaldataSource = new MatTableDataSource([]);
        // project summery
        if (this._sprintData.filterValues.selectedSprintId) {
            this.noSelectedSprint = false;
            this.userService.getSprintDetails(this._sprintData.filterValues.selectedSprintId).then((searchData: any) => {
                console.log('project report component getSprintDetails: ', searchData);
                this.showTable = true;
                this.showLoader = false;
                this.filterChangeService.isLoading.emit({isLoading: false});
                this.responseObject = searchData;
                this.sprintTasks = searchData.sprint_tasks;
                this.sprintTotal = searchData.sprint_total;
                this.sprintName = searchData.sprint_total.sprintName;
                this.sprintDescription = searchData.sprint_total.sprintDescription;
                this.sprintTotaldataSource = new MatTableDataSource([this.sprintTotal]);

                this.userTasks = searchData.user_tasks;
                this.sprintTasksDataSource = new MatTableDataSource(this.sprintTasks);
                for (let x in this.userTasks) {
                    // let uniq = {}
                    // this.userTasks[x] = this.userTasks[x].map(obj => !uniq[obj.taskkey] && (uniq[obj.taskkey] = true));
                    // this.dataSource[x] = new MatTableDataSource(this.userTasks[x])


                    var b = [];
                    this.userTasks[x].forEach((a) => {
                        console.log('a', a);
                        if (!b[a.taskkey]) {
                            b[a.taskkey] = a;
                        } else {
                            if (!b[a.taskkey].childTasks) {
                                b[a.taskkey].childTasks = [];
                            }
                            b[a.taskkey].timeSpentSeconds = b[a.taskkey].timeSpentSeconds + a.timeSpentSeconds;
                            // b[a.taskkey].timeoriginalestimateSec = b[a.taskkey].timeoriginalestimateSec + a.timeoriginalestimateSec
                            b[a.taskkey].childTasks.push(a);
                        }
                    });
                    this.userTasks[x] = [];
                    for (let h in b) {
                        this.userTasks[x].push(b[h]);
                    }
                    console.log('this.userTasks[x]', this.userTasks[x]);
                    this.dataSource[x] = new MatTableDataSource(this.userTasks[x]);
                }

                console.log('this.userTasks:::: ', this.userTasks);
            });
        } else {
            this.noSelectedSprint = true;
        }

    }

    formatWorkLog(workLogs) {
        if (workLogs <= 0) {
            return '';
        }
        workLogs = Number(workLogs / 3600);
        if (Math.round(workLogs) !== workLogs) {
            workLogs = workLogs.toFixed(2);
            return Number.parseFloat(workLogs) + 'h';
        }
        return workLogs + 'h';
    }

    sum(key, data) {
        // only sum for these 2 columns
        if (key === 'timeSpentSeconds' || key === 'timeoriginalestimateSec') {
            if (!data || !data.reduce) {
                return 0;
            }
            return data.reduce((a, b) => {
                if (b[key]) {
                    return a + (b[key] || 0);
                }
                return a;
            }, 0);
        } else {
            return 0;
        }
    }

    sprintSummaryFooter(columnName) {
        if (columnName === 'originalEstimate') {
            return this.sprintTotal['originalest'];
        }
        // if(columnName === 'remainingEstimate') {
        //   return this.sprintTotal['timespent'];
        // }

        if (columnName === 'workratio') {
            return this.sprintTotal['workRatio'];
        }

        if (columnName === 'status') {
            return this.sprintTotal['status'];
        }
    }

    goToIssue(issue) {
        // this method will call from both tables but key name is differnt
        let issueKey = issue.taskkey ? issue.taskkey : issue.key;
        AP.getLocation(function (location) {
            console.log(location);
            let loc = location;
            loc = loc.split('plugins')[0];
            window.open(loc + 'browse/' + issueKey, '_blank');
        });
    }

    formatDate(disCol, columnDate) {
        if (columnDate) {
            return this.datePipe.transform(new Date(columnDate), this.dateService.dateFormatForMenuFilterDate);
        }
        return '';

    }

    // only for dialog box close button
    onCloseButtonClick(): void {
        this.dialogRef.close();
    }
}
