import {Component, ElementRef, ViewChild} from '@angular/core';
import {UserService} from './services/service';
import {FilterChangeService} from './services/common.service';

import {MatSidenav} from '@angular/material/sidenav';

declare var AP: any;

@Component({
    selector: 'app-root',
    templateUrl: './app.component.html',
    styleUrls: ['./app.component.css']
})
export class AppComponent {
    isLoading = false;
    sprintData;
    selectedComponentToShow = 'reportTYpe';
    @ViewChild('drawer') drawer: MatSidenav;

    constructor(private userService: UserService,
                private filterChangeService: FilterChangeService) {
    }

    ngOnInit() {
        this.filterChangeService.change.subscribe((changeData: any) => {
            this.selectedComponentToShow = changeData.filterValues.reportTYpe;
            this.sprintData = changeData;
        });
        this.filterChangeService.isLoading.subscribe((changeData: any) => {
            this.isLoading = changeData.isLoading;
            // this.drawer.close();
        });
        this.filterChangeService.emptyView.subscribe((changeData: any) => {
            this.selectedComponentToShow = changeData.filterValues.emptyView;
            // this.sprintData = changeData;
        });

    }

}
